/**
 * @author tingglelaoo
 * @date 2018-5-22
 * @desc Â±èÂπïÁä∂ÊÄÅ‰∏Ä‰∫õÂ§ÑÁêÜËøõË°åÁªü‰∏ÄÂ∞ÅË£ÖÔºåÂåÖÊã¨‰ª•‰∏ãÂÜÖÂÆπÔºö
 * 1.ÂØπDOMÁªìÊûÑÔºåËøõË°åÂº∫Âà∂Ê®™Á´ñÂ±èÊòæÁ§∫
 * 2.Ê†πÊçÆÈúÄË¶ÅËÆæÁΩÆÊ®™Á´ñÂ±èÁøªËΩ¨ÊèêÁ§∫ËíôÂ±ÇÔºåÊúâÈªòËÆ§ÈÖçÁΩÆÔºàÂèØÈÖçÁΩÆlogoÂíåÊèêÁ§∫ÊñáÊ°àÔºâ
 * 3.Ê®™Á´ñÂ±èÁøªËΩ¨‰∫ã‰ª∂ÂõûË∞ÉÁªëÂÆö
 * @example
  const xScreen = new Screen()
  // Âº∫Âà∂Á´ñÂ±è
  xScreen.lock({
    mode: 'portrait',
    $wrapper: document.getElementById('J_portrait'),
    zIndex: 302,
  })

  // Âº∫Âà∂Ê®™Â±è
  xScreen.lock({
    mode: 'landscape',
    $wrapper: document.getElementById('J_landscape'),
    zIndex: 301,
  })

  // Ê®™Â±èÊèêÁ§∫
  xScreen.inform({
    mode: 'landscape',
    id: 'J_landscapeTips',
  })

  // Á´ñÂ±èÊèêÈÜí
  xScreen.inform({
    mode: 'portrait',
    id: 'J_portraitTips',
    text: 'Á´ñÂ±èÊµèËßà‰ΩìÈ™åÊõ¥Â•ΩÂñîÔºÅ',
    logo: './img/portrait_logo.png'
  })

  // Â±èÂπïÁøªËΩ¨ÂõûË∞É
  xScreen.onOrientationChange(orientation => {
    console.log('1', orientation)
  }, this)

  // Â±èÂπïÁøªËΩ¨ÂõûË∞É
  xScreen.onOrientationChange(orientation => {
    console.log('2', orientation)
  }, this)
 */

export default class Screen {
  constructor () {
    this.$wrappers = [] // Â≠òÂÇ®ÈúÄË¶ÅÂ§ÑÁêÜÁöÑdom
    this.$informs = [] // ÈúÄË¶ÅÊèêÁ§∫ÁöÑ
    this.listeners = []
    this.logoPortrait = `data:image/png;base64,`
    this.logoLandscape = `data:image/png;base64,`

    this.orientation = Screen._detectOrient()
    // ÁªëÂÆö‰∫ã‰ª∂
    window.addEventListener('resize', () => {
      this.orientation = Screen._detectOrient()
      this.$wrappers.forEach(this._lockOrientation.bind(this))
      this.$informs.forEach(this._showInform.bind(this))
      this.listeners.forEach(item => {
        const {
          callback,
          context
        } = item
        callback.call(context, this.orientation)
      })
    }, this)
  }

  static _detectOrient () {
    const screen = window.screen
    const docEle = document.documentElement
    const clientWidth = docEle.clientWidth
    const min = Math.min(screen.width, screen.height)
    const max = Math.max(screen.width, screen.height)

    if (clientWidth === max) {
      return 'landscape'
    }
    if (clientWidth === min) {
      return 'portrait'
    }
    return ''
  }

  static _getLockStyle (mode, zIndex) {
    const docEle = document.documentElement
    const clientWidth = docEle.clientWidth
    const clientHeight = docEle.clientHeight

    let width
    let height
    let rotate
    let originX
    let originY
    switch (mode) {
      case 'portrait':
        width = clientHeight
        height = clientWidth
        rotate = '-90deg'
        originX = clientHeight / 2
        originY = clientHeight / 2
        break
      case 'landscape':
        width = clientHeight
        height = clientWidth
        rotate = '90deg'
        originX = clientWidth / 2
        originY = clientWidth / 2
        break
      default:
        width = clientWidth
        height = clientHeight
        rotate = '0'
        originX = '0'
        originY = '0'
    }

    const cssText =
    `
    position: fixed;
    top: 0;
    left: 0;
    z-index: ${zIndex};
    width: ${width}px;
    height: ${height}px;
    -webkit-transform: rotate(${rotate});
    transform: rotate(${rotate});
    -webkit-transform-origin: ${originX}px ${originY}px;
    transform-origin: ${originX}px ${originY}px;
    `
    return cssText
  }

  static _createTipsStyle (id, zIndex) {
    const cssText =
    `
    #${id}.screentips {
      transition: opacity linear 0.3s;
      pointer-events: none;
      visibility: hidden;
      opacity: 0;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0,0,0, 0.8);
      z-index: ${zIndex};
    }
    #${id}.screentips.show {
      visibility: visible;
      opacity: 1;
      pointer-events: auto;
    }
    #${id} .screentips_inner{
      position: absolute;top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
    }
    #${id} .screentips_icon {
      margin:1.5rem auto;
      display: block;
      width: 5rem;
      animation: phone_rotate 1.6s ease-in infinite;
    }
    #${id} .screentips_icon img {
      width: 100%;
    }
    #${id} .screentips_text {
      white-space: nowrap;
      font-size: 0.75rem;
      text-align: center;
      color: #fff;
    }
    `

    const doc = document
    const style = doc.createElement('style')
    if (style.styleSheet) {
      // IE8 and below.
      style.styleSheet.cssText = cssText
    } else {
      style.appendChild(doc.createTextNode(cssText))
    }
    doc.head.appendChild(style)
  }

  static _createTipsDOM ({ id, text, logo }) {
    return new Promise((resolve, reject) => {
      const doc = document
      const body = doc.body
      const orientDOM = doc.createElement('div')

      orientDOM.id = id
      orientDOM.className = 'screentips'

      const img = new window.Image()
      img.onload = () => {
        orientDOM.innerHTML = `<div class='screentips_inner'><i class='screentips_icon'><img src=${img.src}></i><p class='screentips_text'>${text}</p></div>`
        body.appendChild(orientDOM)
        resolve()
      }
      img.onerror = err => {
        reject(err)
      }
      img.src = logo
    })
  }

  _lockOrientation ({ mode, $wrapper, zIndex }) {
    if (mode === this.orientation) {
      $wrapper.style = Screen._getLockStyle('normal')
      return
    }
    $wrapper.style = Screen._getLockStyle(mode, zIndex)
  }

  _showInform ({ id, mode }) {
    const $inform = document.getElementById(id)
    if (!$inform) return
    if (mode === this.orientation) {
      $inform.classList.remove('show')
    } else {
      $inform.classList.add('show')
    }
  }

  /**
   * @desc Â∞ÜÊåáÂÆöÂÆπÂô®‰øùÊåÅÊ®™Â±è/Á´ñÂ±èÊòæÁ§∫
   * @param {object} config ÈÖçÁΩÆÔºåÂåÖÊã¨‰ª•‰∏ãÂèØÈÖçÁΩÆÈ°πÔºö
   * mode {string} ÊòæÁ§∫Ê®°ÂºèÔºåÂº∫Âà∂Ê®™Â±èÈÄâ"landscape"ÔºåÂº∫Âà∂Á´ñÂ±èÈÄâ"portrait"ÔºåÂøÖÂ°´
   * $wrapper {HTMLElement} Ë¢´Â§ÑÁêÜÁöÑDOMÂÆπÂô®ÔºåÂøÖÂ°´
   * zIndex {number} ÊòæÁ§∫ÁöÑÂ±ÇÊ¨°Ôºå‰πüÂ∞±ÊòØz-indexÔºåÈªòËÆ§ÊòØ301ÔºåÂèØÈÄâ
   */
  lock (config) {
    config = Object.assign({
      mode: '',
      $wrapper: null,
      zIndex: 301
    }, config)

    if (!config.mode || !config.$wrapper) return

    this.$wrappers.push(config)
    this._lockOrientation(config)
  }

  /**
   * @desc Âú®ÊåáÂÆöÂú∫ÊôØ‰∏ãÔºàÊ®™Â±è/Á´ñÂ±èÔºâÊòæÁ§∫ÊèêÈÜíËíôÂ±Ç
   * @param {object} config ÈÖçÁΩÆÔºåÂåÖÊã¨‰ª•‰∏ãÂèØÈÖçÁΩÆÈ°πÔºö
   * mode {string} ÊòæÁ§∫Ê®°ÂºèÔºåÂº∫Âà∂Ê®™Â±èÈÄâ"landscape"ÔºåÂº∫Âà∂Á´ñÂ±èÈÄâ"portrait"ÔºåÂøÖÂ°´
   * id {string} ÊèêÁ§∫ËíôÂ±ÇidÂëΩÂêçÔºåÂøÖÂ°´
   * logo {src} ÊèêÁ§∫ËíôÂ±ÇÁöÑlogoÔºåÈªòËÆ§ÂàáÊç¢Ê®™Á´ñÂ±èÁöÑÈùôÊÄÅlogoÔºåÂèØÈÄâ
   * text {string} ÊèêÁ§∫ÊñáÊ°àÔºåÈªòËÆ§‚Äú‰∏∫‰∫ÜÊõ¥Â•ΩÁöÑ‰ΩìÈ™åÔºåËØ∑‰øùÊåÅÁ´ñÂ±è/Ê®™Â±èÊòæÁ§∫‚ÄùÂèØÈÄâ
   * zIndex {number} ÊòæÁ§∫ÁöÑÂ±ÇÊ¨°Ôºå‰πüÂ∞±ÊòØz-indexÔºåÈªòËÆ§ÊòØ310ÔºåÂèØÈÄâ
   */
  inform ({ mode, id, logo, text, zIndex }) {
    if (!mode || !id) return
    logo = logo || (mode === 'landscape' ? this.logoLandscape : this.logoPortrait)
    text = text || `‰∏∫‰∫ÜÊõ¥Â•ΩÁöÑ‰ΩìÈ™åÔºåËØ∑‰øùÊåÅ${mode === 'landscape' ? 'Ê®™Â±è' : 'Á´ñÂ±è'}ÊµèËßà`
    zIndex = zIndex || 310

    // Ê≥®ÂÖ•CSS
    Screen._createTipsStyle(
      id,
      zIndex
    )

    // Ê≥®ÂÖ•DOM
    Screen._createTipsDOM({
      id,
      text,
      logo
    })
      .then(() => this._showInform({ id, mode }))
      .catch(err => console.warn(err))

    // ÂìçÂ∫îÊ£ÄÊµãÂàóË°®
    this.$informs.push({
      id,
      mode
    })
  }

  /**
   * @desc Ê®™Á´ñÂ±èÂàáÊç¢ÂõûË∞É
   * @param {function} callback ÂõûË∞ÉÂáΩÊï∞
   * @param {variable} context ÂõûË∞ÉÂáΩÊï∞ÁöÑ‰∏ä‰∏ãÊñá
   */
  onOrientationChange (callback, context) {
    if (typeof callback !== 'function') return
    this.listeners.push({
      callback,
      context
    })
  }
}
